"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/Button";
import { formatDuration, formatDateTime } from "@/lib/utils";
import { getHistory } from "@/lib/storage";

export default function CompletePage() {
    const router = useRouter();
    const [mounted, setMounted] = useState(false);
    const [lastSession, setLastSession] = useState<{
        duration: number;
        timestamp: number;
    } | null>(null);

    useEffect(() => {
        setMounted(true);

        // Get the most recent session from history
        const history = getHistory();
        if (history.length > 0 && history[0].completed) {
            setLastSession({
                duration: history[0].duration,
                timestamp: history[0].timestamp,
            });
        }
    }, []);

    const handleStartAnother = () => {
        router.push("/");
    };

    const handleTakeBreak = () => {
        // Just show a nice message and stay on the page
        // Could redirect elsewhere in the future
    };

    if (!mounted) {
        return (
            <div className="min-h-screen flex items-center justify-center">
                <div className="w-8 h-8 border-2 border-success-500 border-t-transparent rounded-full animate-spin" />
            </div>
        );
    }

    return (
        <div className="min-h-screen flex flex-col items-center justify-center px-6 py-12 bg-gradient-to-b from-surface-950 to-surface-900">
            <div className="w-full max-w-md mx-auto text-center space-y-8 animate-fade-in-up">
                {/* Success icon */}
                <div className="mx-auto w-24 h-24 rounded-full bg-success-500/20 flex items-center justify-center glow-success">
                    <svg
                        className="w-12 h-12 text-success-400"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                        strokeWidth={2}
                    >
                        <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            d="M5 13l4 4L19 7"
                        />
                    </svg>
                </div>

                {/* Headline */}
                <div className="space-y-2">
                    <h1 className="text-4xl md:text-5xl font-bold text-white">
                        Session Complete
                    </h1>
                    <p className="text-surface-400 text-lg">
                        You stayed locked. Well done.
                    </p>
                </div>

                {/* Session stats */}
                {lastSession && (
                    <div className="glass rounded-2xl p-6 space-y-4">
                        <div className="flex items-center justify-between">
                            <span className="text-surface-400">Duration</span>
                            <span className="text-white font-semibold">
                                {formatDuration(lastSession.duration)}
                            </span>
                        </div>
                        <div className="h-px bg-surface-700" />
                        <div className="flex items-center justify-between">
                            <span className="text-surface-400">Completed</span>
                            <span className="text-white font-semibold">
                                {formatDateTime(new Date(lastSession.timestamp))}
                            </span>
                        </div>
                    </div>
                )}

                {/* Actions */}
                <div className="flex flex-col gap-3 pt-4">
                    <Button
                        variant="primary"
                        size="lg"
                        className="w-full glow"
                        onClick={handleStartAnother}
                    >
                        Start another session
                    </Button>
                    <Button
                        variant="ghost"
                        size="lg"
                        className="w-full"
                        onClick={handleTakeBreak}
                    >
                        Take a break
                    </Button>
                </div>

                {/* Motivational footer */}
                <p className="text-sm text-surface-500 pt-4">
                    Every completed session builds momentum.
                </p>
            </div>
        </div>
    );
}
